					var wk = new Date(this.starTime).getDay()
					this.typetext1 ='周'+this.weeks[wk];
				}
				// 	var starstr = obj.starTime
				// 	var temp = (new Date(this.endTime) - new Date(starstr))/(24*60*60*1000)
				// 	if(temp>0){
