				// 		this.amountDay = temp
				// 	}else{
				// 		return 
				// 	}
				//}
				

			},
			selectDate(index){
				this.calendarShow = true
				this.type = index
			},
			selectcity(boolean){
				if(boolean){
					this.$router.push({name:'citySelect',query:{path:'home',bool:boolean,scroll:this.scrollTop}})
				}else{
					this.$router.push({name:'citySelect',query:{path:'home'}})
				}
				
			},
			tab(index){
				this.acflag = index
			},
			handleScroll () {
		  	    this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			  //console.log(this.scrollTop)
			    this.$refs.blurbg.style.cssText+='filter:blur('+this.scrollTop/10+'px)'
			    this.$refs.title.style.cssText +='opacity:'+(1-this.scrollTop*5/this.offsetTop)
			    this.$refs.today.style.cssText +='top:'+(this.scrollTop/3)+'px'
			    this.$refs.today.style.cssText +='opacity:'+(this.scrollTop/3>100?0:1)
			},
			gotoDetail(){
				this.$router.push({name:'detailPage'})
			}
			
