			calendar,
			swiper3d,
			bot
		},
		computed:{
		},
		mounted () {
			let that = this
			that.starTime = that.today().toString().split(',').join('-')
			that.endTime = that.tommorrow().toString().split(',').join('-')
			var swiper1 = new Swiper('.swiper-container1', {
		      slidesPerView: 1,
		      spaceBetween: 10,
		      centeredSlides: true,
		    });
		    var swiper2 = new Swiper('.swiper-container2', {
		      slidesPerView: 5,
		      spaceBetween: 0,
		      freeMode: true, 
		    });
		    var swiper3 = new Swiper('.swiper-container3', {
		      slidesPerView: 4,
		      spaceBetween: 0,
		      freeMode: true, 
		    });
		    var swiper4 = new Swiper('.swiper-container4', {
		      slidesPerView: 'auto',
		      spaceBetween: 10,
		      centeredSlides: true,
		    });
		    var swiper5 = new Swiper('.swiper-container5', {
		      slidesPerView: 'auto',
		      spaceBetween: 10,
		      centeredSlides: true,
		    });
		    var swiper6 = new Swiper('.swiper-container6', {
		      slidesPerView: 5,
		      spaceBetween: 10,
		      centeredSlides: true,
		    });
		    var swiper7 = new Swiper('.swiper-container7', {
		      slidesPerView: 'auto',
		      spaceBetween: 15,
		      autoplay:true,
		      effect : 'fade',
		      loop:true,
		      pagination: {
		        el: '.swiper-pagination',
		      }
		    });
		    var swiper8 = new Swiper('.swiper-container8', {
		    	autoplay:true,
		    	loop:true,
		    	effect : 'fade'
		    });
		    swiper6.slideTo(that.defaultFdIndex, 1000, false);
		    that.hzPrice = that.hzList[swiper4.activeIndex].price
		    that.topImg = that.hzList[swiper4.activeIndex].imgUrl
		    that.hzIndex = swiper4.activeIndex+1
		    that.hzCount = swiper4.slides.length
		    that.xfIndex = swiper5.activeIndex+1
		    that.xfCount = swiper5.slides.length
		    swiper4.on('slideChangeTransitionEnd',function(){
		    	that.hzIndex = swiper4.activeIndex+1
		    	that.topImg = that.hzList[swiper4.activeIndex].imgUrl
		    	that.hzPrice = that.hzList[swiper4.activeIndex].price
		    })
		    swiper5.on('slideChangeTransitionEnd',function(){
		    	that.xfIndex = swiper5.activeIndex+1
		    })
		    swiper6.on('slideChangeTransitionEnd',function(){
		    	that.defaultFdIndex = swiper6.activeIndex
		    	that.desac = false
		    	setTimeout(function(args) {
		    		that.desac = true
		    	}, 1000)
		    })
		    window.addEventListener('scroll', this.handleScroll)
		    this.offsetTop = this.$refs.bot.$el.offsetTop
		    //console.log(this.offsetTop)
		},
		watch:{
			scrollTop(val){
				//console.log(val)
				if(val>this.offsetTop){
					this.searchShow = true
				}else{
